  -webkit-align-items: baseline;
          align-items: baseline;
  margin-top: 0;
}

.nav-link {
  margin-right: 0.5rem;
}

#root .nav-link > a {
  color: #fff;
  font-weight: 600;
  transition: .1s color ease;
}

#root .nav-link > a:hover {
  color: #d1d1d1;
}

.nav-content-container {
  max-width: 800px;
}

#root .card-btn {
